import type { SpinalNode } from 'spinal-model-graph';
import type {
  IDiffBimObj,
  IFloorArchi,
  TManualAssingment,
} from '../interfaces/IGetArchi';
import { GEO_REFERENCE_RELATION } from '../../Constant';
import { diffBimObjs } from './diffBimObjs';

export async function getDiffRefFloor(
  floorNode: SpinalNode,
  floorArchi: IFloorArchi,
  manualAssingment: TManualAssingment
): Promise<IDiffBimObj> {
  const floorNodeRefObjs = await floorNode.getChildren(GEO_REFERENCE_RELATION);
  const structures = Object.values(floorArchi.structures).map(
    (itm) => itm.properties
  );
  const diffRef = diffBimObjs(structures, floorNodeRefObjs, manualAssingment);
  return diffRef;
}
