import type { IPreviewObj } from '../../interfaces/IPreviewObj';
import { getModelByModelId } from './getModelByModelId';
import { getBBoxAndMatrix } from './getBBoxAndMatrix';

export async function getBBoxAndMatrixs(
  current: IPreviewObj,
  viewer: Autodesk.Viewing.Viewer3D
): Promise<void> {
  const prom = [];
  for (const item of current.itemToShow) {
    if (typeof item.meshs === 'undefined' || typeof item.bbox === 'undefined') {
      const model = getModelByModelId(item.modelId);
      prom.push(
        getBBoxAndMatrix(item.dbId, model, viewer).then(
          ({ matrixWorld, bbox }) => {
            item.matrixWorld = matrixWorld;
            item.bbox = bbox;
          }
        )
      );
    }
  }
  await Promise.all(prom);
}
