import { getDbIdChildren } from './getDbIdChildren';

export function getLeafDbIds(
  model: Autodesk.Viewing.Model,
  rootId?: number | number[]
): { model: Autodesk.Viewing.Model; selection: number[] } {
  const tree = model.getInstanceTree();
  const dbIds = [];

  if (typeof rootId === 'undefined') {
    rootId = [tree.nodeAccess.rootId];
  } else {
    rootId = Array.isArray(rootId) ? rootId : [rootId];
  }
  rootId.forEach((el) => {
    const queue = [el];
    while (queue.length) {
      const id = queue.pop();
      const children = getDbIdChildren(tree, id);
      if (children.length > 0) {
        queue.push(...children);
      } else {
        dbIds.push(id);
      }
    }
  });
  return { model: model, selection: dbIds };
}
