import { getViewer } from '../getViewer';

export function updateLoadedModel(
  loadedModel: Map<string, Promise<Autodesk.Viewing.Model>>
): void {
  const viewer: Autodesk.Viewing.Viewer3D = getViewer();
  if (!viewer) return;
  try {
    const allModel: Autodesk.Viewing.Model[] = viewer.getAllModels();
    for (const model of allModel) {
      const obj =
        spinal.SpinalForgeViewer.bimObjectService.mappingModelIdBimFileId[
          model.id
        ];
      if (obj) {
        const bimFileId = obj.bimFileId;
        loadedModel.set(bimFileId, Promise.resolve(model));
      }
    }
  } catch (error) {
    console.log(
      'updateLoadedModel fail load existing model list, ignore it if no model loaded'
    );
  }
}
