import { loadModelByBimFileId } from './loadModelByBimFileId';

export function getModelByBimFileId(
  bimFileId: string,
  loadedModel: Map<string, Promise<Autodesk.Viewing.Model>>
): Promise<Autodesk.Viewing.Model> {
  if (loadedModel.has(bimFileId)) return loadedModel.get(bimFileId);
  const prom = loadModelByBimFileId(bimFileId);
  loadedModel.set(bimFileId, prom);
  return prom;
}
